
import Scroller from 'vux/src/components/scroller/index.vue'

import http from '@/axios'

export default {
name:'activity_flow_team',
components:{Scroller},
props:["count"],
data(){
return{
team:[],
teamLength:0,
}
},
methods:{
delTeamer(i){
var userActivityId  = this.team.cooperUser[i].userActivityId;
http.removeTeamerFromSynergetic(userActivityId,(response)=>{
this.team = this.team.splice(i,1);
})
},
addTeamer(){
alert(window.location.href + "&code=" + this.team.coopInviteCode);
}
},
created(){
var param = {};
var self = this;
http.getSynergeticActivityTeam(this.$route.query.id,param,(response)=>{
self.team = response.data;
self.teamLength = response.data.cooperUser.length;
});
}
}



